@import "@share/style/s.module.scss";

html,
body {
  scroll-behavior: smooth;
}

.screen {
  min-height: 100vh;
  position: relative;
}

.screenTop {
  background: url("./bg.png") center 0px no-repeat;
  background-size: cover;
  box-sizing: border-box;
}

.screen1 {
  background: url("./s19.png") center 0px no-repeat;
  background-size: cover;
  box-sizing: border-box;
}

.screen2 {
  background: url("./a10.png") center 0px no-repeat;
  background-size: cover;
  box-sizing: border-box;
}

.screen3 {
  background: url("./m30.png") center 0px no-repeat;
  background-size: cover;
  box-sizing: border-box;
}

.wrap {
  background: #000;
  color: #fff;
}

.wrap h1 {
  font-size: 24px;
  color: #fff;
  display: inline-block;
  padding-right: 20px;
}

.mark {
  position: absolute;
  width: 100%;
  height: 100%;
  //background: rgba($color: #000000, $alpha: 0.5);
}

.top {
  padding: 20px 0 0 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  text-shadow: 0 0 2px #000;
}

.top .inner {
  display: flex;
  flex-direction: row;
}

.inner {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.topMid {
  flex-grow: 1;
  line-height: 24px;
  padding-left: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-weight: 500;
}

.topMid span {
  margin-right: 40px;
  cursor: pointer;
  min-width: 140px;
  text-align: center;
  transition: all linear 100ms;
  color: #ddd;
}
.topMid span:hover {
  font-weight: bold;
  color: #fff;
}

.topMid span.active {
  font-weight: bold;
  color: #fff;
}

.topRight {
  font-size: 20px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  align-items: center;
  width: 138px;
}

.drawer {
  height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: stretch;
}
.drawer li {
  line-height: 50px;
  font-size: 16px;
  color: $mainColor;
  border-bottom: 1px solid #eee;
  cursor: pointer;
}

.drawer li:hover {
  color: #000;
}

.screen1 {
  color: #fff;
}

.intro1 {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-shadow: 0 0 5px #000;
  padding: 10px;
  border-radius: 10px;
  font-size: 13px;
}
.intro1 h3 {
  color: #fff;
  font-size: 40px;
  font-weight: 500;
}

.introTop {
  position: absolute;
  left: 50%;
  transform: translateX(-50%) translateY(-70%);
  top: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-shadow: 0 0 5px #000;
  padding: 10px;
  border-radius: 10px;
  font-size: 14px;
  opacity: 0;
  transition: all linear 300ms;
}

.introTopShow {
  transform: translateX(-50%) translateY(-50%);
  opacity: 1;
}

.introTop h3 {
  color: #fff;
  font-size: 40px;
  font-weight: 500;
  margin-bottom: 10px;
}

.login {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 14px;
  padding: 0 20px;
}

.login span {
  padding: 0 2px;
}

.moveUp {
  position: fixed;
  color: #fff;
  animation: up 1s infinite;
  cursor: pointer;
  z-index: 9999;
  width: 24px;
  height: 24px;
  font-size: 24px;
  display: block;
  margin: 0 auto;
  left: 50%;
  margin-left: -12px;
  bottom: 10px;
  transform: all line 2s;
}

.moveUpHide {
  opacity: 0;
}

.order {
  position: fixed;
  color: #fff;
  cursor: pointer;
  z-index: 9999;
  display: block;
  margin: 0 auto;
  left: 50%;
  transform: translateX(-50%);
  bottom: 50px;
  transform: all line 2s;
  display: flex;
  flex-direction: row;
}

.order span {
  margin: 0 30px;
}

.maskTop {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba($color: #000000, $alpha: 0.5);
  animation: show 8s infinite;
}

@keyframes up {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0.7;
  }
  100% {
    opacity: 0;
  }
}

.btnLeft {
  transition: all linear 300ms;
  transform: translateX(-20%);
  opacity: 0;
}

.btnLeftShow {
  transform: translateX(0);
  opacity: 1;
}

.btnRight {
  transition: all linear 300ms;
  transform: translateX(20%);
  opacity: 0;
}

.btnRightShow {
  transform: translateX(0);
  opacity: 1;
}

@media only screen and (max-width: 600px) {
  .screen1,
  .screen2,
  .screen3,
  .screenTop {
    background-size: cover;
  }
  .screen {
    min-height: 280px;
  }
  .topMid {
    display: none;
  }
  .inner {
    justify-content: space-between;
  }
  .moveUp {
    display: none;
  }
  .order {
    flex-direction: column;
    bottom: 20px;
  }
  .order span {
    margin-top: 10px;
  }
  .introTop {
    width: 90%;
    font-size: 14px;
  }
  .introTop h3 {
    font-size: 20px;
    font-weight: bold;
  }
  .screen3 {
    padding-bottom: 100px;
    box-sizing: content-box;
  }
  .intro1 {
    width: 90%;
    font-size: 18px;
  }
  .intro1 h3 {
    font-size: 30px;
  }
}
