$mainColor: #393c41;

.btnBgDark {
  display: inline-block;
  width: 220px;
  line-height: 40px;
  border-radius: 20px;
  background: rgba(23, 26, 32, 0.8);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.3);
  font-size: 12px;
  text-align: center;
  transition: all linear 300ms;
  font-weight: bold;
}

.btnBgDark:hover {
  background: rgba(23, 26, 32, 1);
  border: 1px solid rgba(255, 255, 255, 0.7);
}

.btnBgLight {
  display: inline-block;
  width: 220px;
  line-height: 40px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.8);
  color: $mainColor;
  border: 1px solid rgba(255, 255, 255, 0.3);
  font-size: 12px;
  text-align: center;
  font-weight: bold;
  transition: all linear 300ms;
}

.btnBgLight:hover {
  background: rgba(255, 255, 255, 1);
  border: 1px solid rgba(255, 255, 255, 0.7);
}
